@charset "UTF- 8";

:root {
    --cor1: rgb(120, 208, 235);
}

* {
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

body {
    background-color: var(--cor1);
}

h1 {
    padding: 0px;
    margin-bottom: 40px;
    text-align: center;
}

p{
    margin-bottom: 10px;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
}

form div {
    display: flex;
    flex-flow: column nowrap;
}

form {
    display: grid;
    width: 100%;
    gap: 10px;
    grid-template-columns: 1fr 1fr;
}

form input {
    border: 2px solid blue;
    height: 30px;
    border-radius: 8px;
}

form input:focus {
    outline: 1px solid black;
    border: 1px solid blue;
}

form button {
    grid-column: 1 / 3;
    border: none;
    background-color: blue;
    color: white;
    font-weight: bold;
    font-size: 1.3em;
    border: 2px solid black;
    border-radius: 8px;
}

form button:hover {
    cursor: pointer;
    background-color: rgb(0, 0, 138);
}

fomr button:active {
    background-color: rgb(0, 0, 0);
}

.resultado{
    margin-bottom: 40px;
    font-size: 3em;
    color: violet;
    font-weight: bold;
    text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":";AACA,iBAAiB;;AAGjB;IACI,0BAA0B;AAC9B;;AAEA;IACI,sBAAsB;IACtB,UAAU;IACV,SAAS;IACT,UAAU;IACV,oCAAoC;AACxC;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,WAAW;IACX,SAAS;IACT,8BAA8B;AAClC;;AAEA;IACI,sBAAsB;IACtB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,wBAAwB;IACxB,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,sBAAsB;IACtB,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;IAChB,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,gCAAgC;AACpC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,aAAa;IACb,iBAAiB;IACjB,kBAAkB;AACtB","sourcesContent":["\r\n@charset \"UTF- 8\";\r\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n:root {\r\n    --cor1: rgb(120, 208, 235);\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    outline: 0;\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: 'Open Sans', sans-serif;\r\n}\r\n\r\nbody {\r\n    background-color: var(--cor1);\r\n}\r\n\r\nh1 {\r\n    padding: 0px;\r\n    margin-bottom: 40px;\r\n    text-align: center;\r\n}\r\n\r\np{\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.container {\r\n    max-width: 640px;\r\n    margin: 50px auto;\r\n    background-color: white;\r\n    border-radius: 10px;\r\n    padding: 20px;\r\n}\r\n\r\nform div {\r\n    display: flex;\r\n    flex-flow: column nowrap;\r\n}\r\n\r\nform {\r\n    display: grid;\r\n    width: 100%;\r\n    gap: 10px;\r\n    grid-template-columns: 1fr 1fr;\r\n}\r\n\r\nform input {\r\n    border: 2px solid blue;\r\n    height: 30px;\r\n    border-radius: 8px;\r\n}\r\n\r\nform input:focus {\r\n    outline: 1px solid black;\r\n    border: 1px solid blue;\r\n}\r\n\r\nform button {\r\n    grid-column: 1 / 3;\r\n    border: none;\r\n    background-color: blue;\r\n    color: 